import axios from "axios";

export default {
  namespaced: true,
  state: {
    items: [
      {
        route: "quizcast",
        title: "Quizcast -  ",
      },
      {
        route: "evolventalux",
        title: "Evolventalux -  ",
      },
      {
        route: "scanchange",
        title: "ScanChange -  ",
      },
      {
        route: "jpbooks",
        title: "  JPBooks",
      },
      {
        route: "hamiltonhouse",
        title: "Hamilton House Publishers",
      },
      // {
      //   route: "autotras",
      //   title: "    - Autotras"
      // },
      {
        route: "bot-for-vk",
        title: "Bot  vk.com",
      },
    ],
  },
  getters: {
    all(state) {
      return state.items;
    },
    get: (state) => (route) => {
      return state.items.find((one) => one.route === route);
    },
  },
  actions: {
    loadProject(store, data) {
      const project = store.getters.get(data.name);
      return axios.get(`/projects/${project.route}.md`);
    },
  },
};
